

























import {
  computed, defineComponent, PropType, Ref,
} from '@vue/composition-api';
import {
  ColorBoxEditorControl, CheckboxToggleEditorControl,
} from '@/app/editor/client/view/component/control';
import { Color } from '@/app/shared/client/type/color';
import { GlobalDesign } from '@/app/shared/client/type/tile/global';
import { calculateActualColorDefault } from '@/app/editor/client/logic/defaults';
import { ArrowDefaults, ArrowSettings } from '@/app/shared/client/type/settings/arrow-settings';
import useI18N from '@/app/shared/client/logic/i18n';

function useSolidOverlayColorChangeHandler(
  settings: Readonly<Ref<Readonly<ArrowSettings>>>,
  emit: (event: string, arg: ArrowSettings) => void,
): (value?: Color) => void {
  function onColorChange(value?: Color): void {
    const newSettings = {
      ...settings.value,
      color: value,
    };
    emit('editor-action', newSettings);
  }

  return onColorChange;
}

function useVisibleChangeHandler(
  settings: Readonly<Ref<Readonly<ArrowSettings>>>,
  emit: (event: string, arg: ArrowSettings) => void,
): (value?: boolean) => void {
  function onVisibleChange(value?: boolean): void {
    const newSettings = {
      ...settings.value,
      visible: value,
    };
    emit('editor-action', newSettings);
  }

  return onVisibleChange;
}

const ArrowDesignEditor = defineComponent({
  components: {
    ColorBoxEditorControl,
    CheckboxToggleEditorControl,
  },
  props: {
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<ArrowSettings>,
    },
    defaults: {
      required: true,
      type: Object as PropType<ArrowDefaults>,
    },
    siteSettings: {
      required: false,
      default: undefined,
      type: Object as PropType<GlobalDesign>,
    },
    colorList: {
      required: true,
      type: Array as PropType<string[]>,
    },
    autoColorEnabled: {
      required: true,
      type: Boolean,
    },
    alphaColorEnabled: {
      required: true,
      type: Boolean,
    },
  },
  setup(props, { root, emit }) {
    const i18n = useI18N(root.$store);

    const settings = computed(() => ({
      color: props.value?.color,
      visible: props.value?.visible ?? props.defaults?.visible,
    }));

    const defaultColor = computed(() => calculateActualColorDefault(
      props.defaults?.color,
      props.siteSettings,
    ));
    const color = computed({
      get: () => settings.value?.color,
      set: useSolidOverlayColorChangeHandler(settings, emit),
    });

    const visibility = computed({
      get: () => settings.value.visible,
      set: useVisibleChangeHandler(settings, emit),
    });

    return {
      i18n,
      color,
      defaultColor,
      visibility,
    };
  },
});

export default ArrowDesignEditor;
